document.querySelectorAll(".delete-entity-button").forEach(function (btn) {
    btn.addEventListener('click', () => {
        const id = btn.dataset.idEntity;
        const url = btn.dataset.urlEntity;
        const date = btn.dataset.dateEntity;
        console.log(url);
        if (confirm(`Вы уверены что хотите удалить новость от ${date} даты?`)) {
            fetch(url+id, {
                method: 'DELETE'
            }).then(response => response.json())
            .then(result => {
                if (result.status) {
                    document.location.reload();
                } else {
                    alert('Кавальски всё пощло не по плану');
                }
            });
        } 
    })
})